let ajaxConfig = {
    timeout: 0
};

export function ajaxSetup(args) {
    // |args| may include: timeout.
    if (Object.prototype.hasOwnProperty.call(args, 'timeout')) {
        ajaxConfig.timeout = args.timeout;
    }
}

export const AJAX_REQUEST_VERB = {
    GET: "GET",
    POST: "POST"
}

export function ajax(args) {
    // |args| must include: url.
    // |args| may include: type (=AJAX_REQUEST_VERB.*), dataType, data, timeout,
    // success, error.
    let xmlhttp = new XMLHttpRequest();

    const verb = Object.prototype.hasOwnProperty.call(args, 'type') ? args.type
                                             : AJAX_REQUEST_VERB.GET;
    xmlhttp.open(verb, args.url, true);

    if (Object.prototype.hasOwnProperty.call(args, 'dataType')) {
        xmlhttp.responseType = args.dataType;
    }
    if (Object.prototype.hasOwnProperty.call(args, 'timeout')) {
        xmlhttp.timeout = args.timeout;
    } else if (ajaxConfig.timeout > 0) {
        xmlhttp.timeout = ajaxConfig.timeout;
    }

    xmlhttp.onreadystatechange = function() {
        if (xmlhttp.readyState == 4) {   // 4 == done
            if (Math.floor(xmlhttp.status / 100) == 2 && xmlhttp.response) {
                if (Object.prototype.hasOwnProperty.call(args, 'success')) {
                    args.success(xmlhttp.response);
                }
            } else {
                if (Object.prototype.hasOwnProperty.call(args, 'error')) {
                    args.error();
                }
            }
        }
    }

    xmlhttp.send(Object.prototype.hasOwnProperty.call(args, 'data')? args.data : null);
}
