import {ajax} from '../../utilities/src/ajax';

const spaceRegExp = new RegExp("^[\\s" + unescape("\u3000") + "]+");

export class RSuggest {
	constructor() {
		this.cachedResults = {};
		this.backup = "";
		this.lastInput = "";
		this.respInput = "";
		this.sggstSelectNum = -1;
		this.usingChar = "";
		this.mouseDownWasAtElem = null;
		this.mouseState = 0;
		this.maxIndex = 0;
		this.index = -1;
		this.viewEvent = null;
		this.ajaxEvent = null;
		this.suggestDivPos = null;
		this.submitWay = 0;
		this.blackList = [];
		this.userInteractedWithInput = false;
	}

	preInit() {
		this.suggestDivPos = this.input.parentElement;
		return true;
	}

	escapeHtml(val) {
		const elm = document.createElement('div')
		elm.textContent = val;
		return elm.innerHTML;
	}

	initFailure() {
		this.input.setAttribute("autocomplete", "");
	}

	createSuggestDiv() {
		this.suggest = document.createElement("div");
		this.suggest.setAttribute("id", "suggestbox");
		this.suggest.style.display = "none";
		this.suggestDivPos.appendChild(this.suggest);

		for (let i = 0; i < this.count; ++i) {
			this.addNewRow();
		}
	}

	addNewRow() {
		const cssKeywordElem = document.createElement("span");
		cssKeywordElem.setAttribute("class", this.cssKeyword);

		cssKeywordElem.addEventListener("mouseup", (e) => {
			if (e.which != 1 || this.mouseDownWasAtElem != e.currentTarget) {
				return;
			}
			this.mouseState = 2;
			this.input.blur();
			this.input.value = e.currentTarget.textContent;
			this.sggstSelectNum = this.index;
			this.usingChar = e.currentTarget.textContent;
			this.submitWay = 2;

			this.onRowKeywordLaunch(e.currentTarget);
			this.suggest.style.display = "none";

			setTimeout(() => {
				this.button.click();
			}, 10);

			this.eventStop();
		});

		cssKeywordElem.addEventListener("mousedown", (e) => {
			if (e.which != 1) {
				this.input.blur();
				return;
			}
			this.mouseDownWasAtElem = e.currentTarget;
			this.mouseState = 1;
			e.preventDefault();
		});

		cssKeywordElem.addEventListener("mouseover", (e) => {
			this.over(e.currentTarget);
		});

		cssKeywordElem.addEventListener("mouseout", (e) => {
			this.mouseState = 0;
		});

		const cssNoSelectElem = document.createElement("div");
		cssNoSelectElem.setAttribute("class", this.cssNoSelect);
		cssNoSelectElem.appendChild(cssKeywordElem);

		const rowElem = document.createElement("div");
		rowElem.appendChild(cssNoSelectElem);

		this.suggest.appendChild(rowElem);
		return rowElem;
	}

	onRowKeywordLaunch(rowKeywordElem) {
	}

	onEmptyInput() {
		this.hide();
	}

	doDisplayOrHideIfEmpty() {
		if (this.maxIndex > 0) {
			this.suggest.style.display = "block";
		} else {
			this.hide();
		}
	}

	search() {
		const k = this.input.value;
		if (k == "" || k.replace(spaceRegExp, "").length == 0) {
			this.usingChar = "";
			this.sggstSelectNum = -1;
			this.onEmptyInput(this.lastInput.length == 0);
			this.lastInput = "";
			return;
		}

		if (k == this.lastInput) {
			return;
		}

		this.lastInput = k;
		this.backup = k;
		clearTimeout(this.ajaxEvent);
		this.ajaxEvent = setTimeout(() => {
			const q = this.lastInput.replace(spaceRegExp, "");
			if (q.length == 0) {
				return;
			}
			if (!Object.prototype.hasOwnProperty.call(this.cachedResults, q)) {
				let u = this.baseUrl
							+ "?" + this.paramSuggestid + "=" + this.suggestId
							+ "&" + this.paramInput + "=" + encodeURIComponent(q)
							+ "&" + this.paramCallback + "=json";
							if (this.exParam) {
								u += "&" + this.exParam;
							}
				this.read(u, q);
			} else {
				this.show(this.cachedResults[q], q, /*from_cache=*/true);
			}
		}, 10);
	}

	read(jsonUrl, q) {
		ajax({
			url: jsonUrl,
			cache: true,
			timeout: 10000,
			scriptCharset: this.encoding,
			success: (cb_json) => {
				if (q) {
					let data = {};
					if (cb_json && cb_json.length > 1) {
						try {
							data = JSON.parse(cb_json);
						} catch(e) {
						}
					}
					this.show(data, q, /*from_cache=*/false);
				}
			}
		});
	}

	eventStart() {
		clearInterval(this.viewEvent);

		this.viewEvent = setInterval(() => {
			this.search();
		}, 100);
	}

	eventStop() {
		clearInterval(this.viewEvent);
		setTimeout(() => {
			this.hide();
		}, 10);
	}

	show(data, key, from_cache) {
		if (!from_cache) {
			if (typeof data.status == "undefined" ||
					data.status != "OK") {
				this.maxIndex = 0;
				this.doDisplayOrHideIfEmpty();
				return;
			}

			this.cachedResults[key] = data;
		}

		if (this.lastInput.replace(spaceRegExp, "") != key) {
			return;
		}

		let len = data.suggestions.length;
		if (len == 0) {
			this.maxIndex = 0;
			this.doDisplayOrHideIfEmpty();
			return;
		}
		if (len > this.count) {
			len = this.count;
		}

		this.maxIndex = len;
		this.index = -1;
		this.respInput = this.lastInput;

		const allKeywordElems =
			this.suggest.getElementsByClassName(this.cssKeyword);
		for (let i = 0; i < allKeywordElems.length; ++i) {
			const keywordElem = allKeywordElems[i];
			keywordElem.parentElement.setAttribute("class", this.cssNoSelect);
			if (i < len) {
				const text = data.suggestions[i].terms;
				const strongkey = this.lastInput.toLowerCase();
				if (text.startsWith(strongkey)) {
					let textWithBoldKeyword =
						"<strong>" + this.escapeHtml(strongkey) + "</strong>";
					textWithBoldKeyword +=
						this.escapeHtml(text.substr(strongkey.length));
					keywordElem.innerHTML = textWithBoldKeyword;
				} else {
					keywordElem.textContent = text;
				}
				keywordElem.parentElement.style.display = "block";
			} else {
				keywordElem.parentElement.style.display = "none";
			}
		}

		this.doDisplayOrHideIfEmpty();
	}

	hide(revertInput) {
		if (revertInput && this.lastInput.length != 0) {
			this.input.value = this.backup;
			this.sggstSelectNum = -1;
			this.usingChar = "";
		}

		this.lastInput = this.input.value;
		this.suggest.style.display = "none";
	}

	select(fillInput) {
		const allKeywordElems =
			this.suggest.getElementsByClassName(this.cssKeyword);
		for (let i = 0; i < allKeywordElems.length; ++i) {
			const keywordElem = allKeywordElems[i];
			keywordElem.parentElement.classList.remove(this.cssSelect);
			keywordElem.parentElement.classList.add(this.cssNoSelect);
		}

		let selectedKeywordElem = null;
		if (this.index >= 0 && this.index < this.maxIndex &&
				this.index < allKeywordElems.length) {
			selectedKeywordElem = allKeywordElems[this.index];
			selectedKeywordElem.parentElement.classList.remove(this.cssNoSelect);
			selectedKeywordElem.parentElement.classList.add(this.cssSelect);
		}

		if (fillInput) {
			let d = "";
			if (selectedKeywordElem) {
				d = selectedKeywordElem.textContent;
				this.usingChar = d;
				this.sggstSelectNum = this.index;
			} else {
				d = this.backup;
				this.usingChar = "";
				this.sggstSelectNum = -1;
			}
			this.input.value = d;
			this.lastInput = d;
		}
	}

	up() {
		if (this.maxIndex == 0) {
			return;
		}

		if (this.index < 0) {
			this.index = this.maxIndex;
		}

		if (this.suggest.style.display == "block") {
			this.index--;
			this.select(/*fillInput=*/true);
		} else if (this.respInput == this.lastInput) {
			this.select();
			this.suggest.style.display = "block";
		}
	}

	down() {
		if (this.maxIndex == 0) {
			return;
		}

		if (this.index >= this.maxIndex) {
			this.index = -1;
		}

		if (this.suggest.style.display == "block") {
			this.index++;
			this.select(/*fillInput=*/true);
		} else if (this.respInput == this.lastInput) {
			this.select();
			this.suggest.style.display = "block";
		}
	}

	over(rowKeywordElem) {
		const allKeywordElems =
			this.suggest.getElementsByClassName(this.cssKeyword);
		this.index =
			Array.prototype.indexOf.call(allKeywordElems, rowKeywordElem);
		this.select();
	}

	jump() {
		const usingSuggestionOrHistory = this.isUsingInputChars();
		this.sendSubmitLog(usingSuggestionOrHistory);
	}

	sendSubmitLog(suggestUse) {
		const q = this.input.value.replace(spaceRegExp, "");
		let u = this.baseUrl + "/" + this.submitLogCommand
					+ "?" + this.paramSuggestid + "=" + this.suggestId
					+ "&" + this.paramCollection + "=" + this.col
					+ "&" + this.paramReqid + "=" + this.sggstReqid
					+ "&" + this.paramSelectNum + "=" + this.sggstSelectNum
					+ "&" + this.paramSelect + "=" + this.usingChar
					+ "&" + this.paramSuggestUse + "=" + suggestUse
					+ "&" + this.paramSubmitWay + "=" + this.submitWay
					+ "&" + this.paramInput + "=" + encodeURIComponent(q)
					+ "&" + this.paramCallback + "=" + this.paramCallback;
		if (this.sendFormParam) {
			/* TODO: implement form serialization method.
			u += "&" + this.paramFormParam + "=" +
					encodeURIComponent(this.form.serialize());
			*/
		}
		if (this.exParam) {
			u += "&" + this.paramExParam + "=" +
					encodeURIComponent(this.exParam);
		}
		this.read(u);
	}

	isUsingInputChars() {
		return this.usingChar.length > 0 &&
				this.input.value.startsWith(this.usingChar);
	}

	isUserAgentGood() {
		const ua = navigator.userAgent.toLowerCase();
		for (let bad_ua of this.blackList) {
			let re = new RegExp("(" + bad_ua + ")");
			if (re.exec(ua) != null) {
				return false;
			}
		}
		return true;
	}

	init() {
		this.config = document.getElementById("sggstConfig");
		this.input = document.getElementById(this.config.getAttribute("input"));
		this.input.setAttribute("autocomplete", "off");
		this.input.setAttribute("maxlength", "2048");

		if (!this.preInit()) {
			this.initFailure();
			return;
		}

		// Required config attributes.
		try {
			this.form = document.getElementById(
				this.config.getAttribute("form"));
			this.button = document.getElementById(
				this.config.getAttribute("button"));
			this.baseUrl = this.config.getAttribute("baseUrl");
			this.col = this.config.getAttribute("col");
			this.suggestId = this.config.getAttribute("suggestId");
			this.encoding = this.config.getAttribute("encoding");
		} catch(e) {
		}
		const isNonStringOrEmpty = function(v) {
			return typeof v != "string" || v.length == 0;
		};
		if (!this.form || !this.button ||
				isNonStringOrEmpty(this.baseUrl) ||
				isNonStringOrEmpty(this.col) ||
				isNonStringOrEmpty(this.suggestId) ||
				isNonStringOrEmpty(this.encoding) ||
				!this.isUserAgentGood() ||
				!this.input.parentElement.classList.contains("sggstInputWrap")) {
			this.initFailure();
			return;
		}

		this.exParam = this.config.getAttribute("exParam");
		this.sendFormParam =
			this.config.getAttribute("sendFormParam") == "1";

		this.count = 10;
		this.cssSelect = "sggstSelect";
		this.cssNoSelect = "sggstNoselect";
		this.cssKeyword = "sggstKeyword";
		this.paramInput = "q";
		this.paramCollection = "cl";
		this.paramSuggestid = "sid";
		this.paramSuggestUse = "su";
		this.paramSelectNum = "sn";
		this.paramFormParam = "fp";
		this.paramReqid = "rid";
		this.sggstReqid = Math.floor(Math.random() * 10000000000);
		this.paramSelect = "ss";
		this.searchCommand = "suggest";
		this.submitLogCommand ="submit";
		this.paramSubmitWay = "sw";
		this.paramCallback = "format";
		this.paramExParam = "ex";

		this.createSuggestDiv();

		this.input.addEventListener("mousedown", (e) => {
			this.userInteractedWithInput = true;
		});

		this.input.addEventListener("keydown", (e) => {
			this.userInteractedWithInput = true;
			switch (e.keyCode) {
			case 40:  // ARROW DOWN
				this.down();
				break;
			case 38:  // ARROW UP
				this.up();
				break;
			case 9:  // TAB
				this.hide();
				break;
			case 27:  // ESC
				this.hide(true);
				e.preventDefault();
				break;
			case 13:  // ENTER
				this.submitWay = 1;
				this.onRowKeywordLaunch();
				this.suggest.style.display = "none";
				setTimeout(() => {
					this.button.click();
				}, 10);
				this.eventStop();
				e.preventDefault();
				break;
			}
		});

		this.input.addEventListener("focus", () => {
			this.lastInput = this.input.value;
			this.backup = this.lastInput;
			this.eventStart();
		});

		this.input.addEventListener("blur", () => {
			if (this.mouseState == 0) {
				this.eventStop();
			} else if (this.mouseState == 1) {
				this.input.focus();
				this.mouseState = 0;
			}
		});

		this.button.addEventListener("click", (e) => {
			this.jump();
		});

		// Re-focus to exec our event handlers.
		if (document.activeElement == this.input) {
			this.input.blur();
			this.input.focus();
		}
	}
}
